    display: flex;

    justify-content: center;
    align-items: center;
    height: 100vh;

    #login-fm {
        width: 300px;
        /*height: 200px;*/
        .header {
            text-align: center;
            margin-bottom: 20px;
            h1 {
                margin-bottom: 10px;
                font-weight: 300;
                font-size: 30px;
                color: #17233d;
            }
            p {
                color: #808695;
                font-weight: 300;
            }
        }
        .body {
            .forgetpwd {
